import { supabase } from '@/lib/supabase' // corriger le chemin si besoin

type ImportantDate = {
  id: number        // id en base (souvent bigint / int)
  type: string
  date: string      // Supabase renvoie un timestamp en string ISO
}

export default async function TempsPage() {
  const { data, error } = await supabase
    .from('important_dates')
    .select<'*', ImportantDate>() // optionnel mais typé
    .order('date', { ascending: true })

  if (error) {
    return (
      <main className="p-6 text-red-500">
        Erreur : {error.message}
      </main>
    )
  }

  return (
    <main className="p-6">
      <h1 className="text-2xl font-bold mb-6">
        Nos dates importantes ⏳
      </h1>

      <div className="overflow-x-auto">
        <table className="w-full border-collapse">
          <thead>
            <tr className="bg-gray-100 text-left">
              <th className="p-3 border">Type</th>
              <th className="p-3 border">Date</th>
            </tr>
          </thead>

          <tbody>
            {data?.map((item) => (
              <tr key={item.id} className="border-b">
                <td className="p-3 border capitalize">
                  {item.type.replace('_', ' ')}
                </td>
                <td className="p-3 border">
                  {new Date(item.date).toLocaleDateString('fr-FR')}
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </main>
  )
}
